<template>
  <div class="newsinfo-content">
    <h3 class="title">{{newsInfo.title}}</h3>
    <p class="subtitle">
      <span>发表时间：{{newsInfo.add_time | dataTime}}</span>
      <span>点击：{{newsInfo.click}}次</span>
    </p>
    <hr>
    <div class="content-detial" v-html="newsInfo.content"></div>
    <!-- 品论区 -->
    <commentBox :newsId='id'></commentBox>
  </div>
</template>

<script>
import { Toast } from "mint-ui"
// 引入品论子组件
import comment from "../subcomponents/comment"
export default {
  data() {
    return {
      id: this.$route.params.id,
      newsInfo: {}
    }
  },
  created() {
    this.getnewsInfo();
  },
  methods: {
    getnewsInfo() {
      this.$http.get("api/getnew/" + this.id).then(res => {
        if (res.body.status === 0) {
          this.newsInfo = res.body.message[0]
        } else {
          Toast("数据获取失败")
        }
      });
    }
  },
  components:{
      commentBox:comment
  }
}
</script>

<style lang="less" scoped>
.newsinfo-content {
  padding: 0 5px;
  .title {
    font-size: 16px;
    color: #f90707;
    text-align: center;
    padding: 10px 0;
  }
  .subtitle {
    font-size: 13px;
    color: #06f;
    display: flex;
    justify-content: space-between;
  }
  
}
</style>
