<template>
  <div class="detail-content">
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" ref="ball" v-show="ballFlag" id="ball"></div>
    </transition>
    <div class="mui-card">
      <div class="mui-card-content">
        <swipe :bannerList="bannerList" :isfull="false"></swipe>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-header">{{goodsInfo.title}}</div>
      <div class="mui-card-content price-info">
        <p>
          市场价：
          <del>￥{{goodsInfo.market_price}}</del> &nbsp;&nbsp;&nbsp;&nbsp;销售价：
          <span class="new-price">￥{{goodsInfo.sell_price}}</span>
        </p>
        <p class="purchases">购买数量：
          <numberBox @getCount="getNumberCount" :maxCount="goodsInfo.stock_quantity"></numberBox>
        </p>
        <div class="shopping-btn">
          <mt-button size="small" type="primary">立即购买</mt-button>
          <mt-button size="small" type="danger" @click="addShoppingCar">加入购物车</mt-button>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-header">商品参数</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>商品货号：{{goodsInfo.goods_no}}</p>
          <p>库存情况：{{goodsInfo.stock_quantity}}</p>
          <p>上架时间：{{goodsInfo.add_time | dataTime}}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button size="large" type="primary" plain @click="goodsIntroduce(id)">图文介绍</mt-button>
        <mt-button size="large" type="danger" plain @click="commMore(id)">评论更多</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import swipe from "../subcomponents/swipe";
import numberBox from "../subcomponents/goodsNumberBox";
export default {
  // const ballStartDic = this.$refs.ball
  data() {
    return {
      id: this.$route.params.id,
      bannerList: [],
      goodsInfo: {},
      ballFlag: false,
      ballStratX: null,
      ballStratY: null,
      ballSEndX: null,
      ballSEndY: null,
      numberCount: 1
    };
  },
  created() {
    this.getImgInfo();
    this.getGoodsInfo();
  },
  methods: {
    getImgInfo() {
      this.$http.get("api/getthumimages/" + this.id).then(res => {
        // console.log(res)
        if (res.body.status === 0) {
          res.body.message.forEach((item, i) => {
            item.img = item.src;
          });
          this.bannerList = res.body.message;
        }
      });
    },
    getGoodsInfo() {
      // console.log(this.$refs)
      // console.log(this.$refs.ball)
      this.$http.get("api/goods/getinfo/" + this.id).then(res => {
        if (res.body.status === 0) {
          this.goodsInfo = res.body.message[0];
          // console.log(this.goodsInfo)
        }
      });
    },
    goodsIntroduce(id) {
      this.$router.push("/home/goodsIntroduce/" + id);
    },
    commMore(id) {
      this.$router.push({ path: "/home/commMore/" + id });
    },
    beforeEnter(el) {
      el.style.transform = `translate(0,0)`;
    },
    enter(el, done) {
      this.ballStratX = this.$refs.ball.getBoundingClientRect().left;
      this.ballStratY = this.$refs.ball.getBoundingClientRect().top;
      // console.log(document.getElementById("ball"))
      // this.ballStratX=document.getElementById("ball").getBoundingClientRect().left;
      // this.ballStratY=document.getElementById("ball").getBoundingClientRect().top;
      this.ballEndX = document
        .getElementById("amount")
        .getBoundingClientRect().left;
      this.ballEndY = document
        .getElementById("amount")
        .getBoundingClientRect().top;
      // console.log(this.ballStratX,this.ballStratY,this.ballEndX,this.ballEndY)
      el.offsetWidth;
      el.style.transform = `translate(${this.ballEndX -
        this.ballStratX}px,${this.ballEndY - this.ballStratY}px)`;
      el.style.transition = "all .5s cubic-bezier(.02,-0.61,1,.69)";
      done(); // 结束 立马调用afterenter（）函数
    },
    afterEnter(el) {
      this.ballFlag = false;
    },
    getNumberCount(count) {
      //获取选择的数量，涉及到子组件向父组件传值
      this.numberCount = count;
      // console.log(this.numberCount)
    },
    addShoppingCar() {
      this.ballFlag = !this.ballFlag;
      var shoppingData = {
        id: this.id,
        count: this.numberCount,
        price: this.goodsInfo.sell_price,
        selected: true
      };
      this.$store.commit("addShoppingCar", shoppingData);
    }
  },
  components: {
    swipe,
    numberBox
  }
};
</script>
<style lang="less" scoped>
.detail-content {
  background-color: #eee;
  overflow: hidden;
  .price-info {
    padding: 15px;
    .new-price {
      font-size: 17px;
      color: #f90707;
      font-weight: 700;
    }
    .purchases {
      display: flex;
      line-height: 35px;
    }
    .shopping-btn {
      display: flex;
      justify-content: space-around;
    }
  }
  .mui-card-footer {
    flex-direction: column;
    button {
      margin: 8px 0;
    }
  }
  .ball {
    position: absolute;
    top: 380px;
    left: 148px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #f90707;
    z-index: 99;
  }
}
</style>
