<template>
  <div class="p-info">
    <h4 class="p-info-title">{{info.title}}</h4>
    <p class="p-info-subtitle">
      <span>发表时间：{{info.add_time | dataTime}}</span>
      <span>点击：{{info.click}}次</span>
    </p>
    <hr>
    <vue-preview :slides="slide1" @close="handleClose"></vue-preview>
    <div class="p-info-content" v-html="info.content"></div>
    <comm :id="id"></comm>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import comment from "../subcomponents/comment";
export default {
  data() {
    return {
      id: this.$route.params.id,
      info: {}, // 图片详细
      slide1: []
    };
  },
  created() {
    this.getPhotoInfo();
    this.getThumbnail();
  },
  methods: {
    getPhotoInfo() {
      this.$http.get("api/getimageInfo/" + this.id).then(res => {
        if (res.body.status === 0) {
          this.info = res.body.message[0];
        } else {
          Toast("获取数据失败");
        }
      });
    },
    getThumbnail() {
      this.$http.get("api/getthumimages/" + this.id).then(res => {
        if (res.body.status === 0) {
          // console.log(res);
          res.body.message.forEach((item,i)=>{
            item.msrc= item.src;
            item.alt= 'picture1'+(i+1);
            item.title='Image Caption'+(i+1);
            item.w=600;
            item.h=400;
          })
          this.slide1=res.body.message;
        } else {
          Toast("获取缩略图失败");
        }
      });
    },
    handleClose() {
      console.log("close event");
    }
  },
  components: {
    comm: comment
  }
};
</script>
<style lang="less" scoped>
.p-info {
  padding: 0 6px;
  .p-info-title {
    font-size: 15px;
    color: #00a4ff;
    padding: 15px 0;
    text-align: center;
  }
  .p-info-subtitle {
    display: flex;
    justify-content: space-between;
  }
  .p-info-content {
    font-size: 12px;
    line-height: 28px;
  }
}
</style>
