<template>
  <div>
    <div class="mui-numbox" data-numbox-step='1' data-numbox-min='1'>
      <button class="mui-btn mui-numbox-btn-minus" type="button">-</button>
      <input class="mui-numbox-input" type="number" value="1" ref="getCount" @change="changeCount"/>
      <button class="mui-btn mui-numbox-btn-plus" type="button">+</button>
    </div>
  </div>
</template>
<script>
import js from'../../lib/mui/js/mui.js'
export default {
    mounted() {
        mui(".mui-numbox").numbox()
    },
    methods: {
      changeCount(){
        //  console.log(this.$refs.getCount.value)
        this.$emit("getCount",parseInt(this.$refs.getCount.value));
      }
    },
    props:["maxCount"],
    watch: {
      maxCount:(newVal,oldVal)=>{
        console.log(newVal)
        mui(".mui-numbox").numbox().setOption('max',newVal)  
      }
    },
}
</script>
<style lang="less" scoped>

</style>

