<template>
  <div>
    <div class="mui-numbox" data-numbox-step="1" data-numbox-min="1">
      <button class="mui-btn mui-numbox-btn-minus" type="button">-</button>
      <input
        class="mui-numbox-input"
        type="number"
        :value="goodscount"
        ref="getCount"
        @change="changeCount"
      >
      <button class="mui-btn mui-numbox-btn-plus" type="button">+</button>
    </div>
  </div>
</template>
<script>
import js from "../../lib/mui/js/mui.js";
export default {
  data() {
    return {};
  },
  mounted() {
    mui(".mui-numbox").numbox();
  },
  methods: {
    changeCount() {
      this.$store.commit("updateCar", {
        id: this.goodsid,
        count: this.$refs.getCount.value
      });
      // console.log(this.$refs.getCount.value)
    }
  },
  props: ["goodscount", "goodsid"]
};
</script>
<style lang="less" scoped>

</style>

