<template>
  <div>
    <div id="slider" class="mui-slider">
      <div
        id="sliderSegmentedControl"
        class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted"
      >
        <div class="mui-scroll">
          <a
            :class="['mui-control-item',item.id==0?'mui-active':'']"
            href="#item1mobile"
            data-wid="tab-top-subpage-1.html"
            v-for="item in photoTitle"
            :key="item.id"
            @tap="getPhotoList(item.id)"
          >{{item.title}}</a>
        </div>
      </div>
    </div>
    <ul class="photo-list">
      <router-link v-for="item in list" :key="item.id" tag="li" :to="'/home/photoInfo/'+item.id">
        <img v-lazy="item.img_url">
        <div class="mask">
          <p class="mask-title">{{item.title}}</p>
          <p class="mask-body">{{item.zhaiyao}}</p>
        </div>
      </router-link>
    </ul>
  </div>
</template>
<script>
import mui from "../../lib/mui/js/mui.js";
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      photoTitle: [],
      list: []
    };
  },
  created() {
    this.getPhotoTitle();
    this.getPhotoList(0);
  },
  mounted() {
    // 需要在组件的 mounted 事件钩子中，注册 mui 的 scroll 滚动事件
    mui(".mui-scroll-wrapper").scroll({
      deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
    });
  },
  methods: {
    getPhotoTitle() {
      this.$http.get("api/getimgcategory").then(res => {
        if (res.body.status === 0) {
          res.body.message.unshift({ title: "全部", id: 0 });
          this.photoTitle = res.body.message;
        } else {
          Toast("小老弟没有获取到数据");
        }
      });
    },
    getPhotoList(listId) {
      this.$http.get("api/getimages/" + listId).then(res => {
        if (res.body.status === 0) {
          this.list = res.body.message;
        } else {
          Toast("小老弟没有获取到数据");
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
* {
  touch-action: pan-y;
}
img[lazy="loading"] {
  width: 40px;
  height: 40px;
  margin: auto;
}
.photo-list {
  padding: 0 10px 0 10px;
  margin: 0;
  list-style: none;
  > li {
    position: relative;
    margin-bottom: 10px;
    background-color: #ccc;
    min-height: 250px;
    box-shadow: 0 0 6px #333;
    border-radius: 3px;
     img {
      vertical-align: middle;
      width: 100%;
      max-height: 300px;
    }
    >.mask{
      position: absolute;
      bottom: 0;
      background-color: rgba(0,0,0,.5 );
      .mask-title{
        font-size: 14px;
        color: #fff;
      }
      .mask-body{
        font-size: 13px;
        color: #fff;
      }
    }
  }
}
</style>
