<template>
  <div>
    <mt-swipe :auto="3000">
      <!-- 在组件中，使用v-for循环的话，一定要使用 key -->
      <mt-swipe-item v-for="item in bannerList" :key="item.id">
        <img :src="item.img" alt :class="{'full':isfull}">
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>
<script>
export default {
  props: ["bannerList", "isfull"]
};
</script>
<style lang="less" scoped>
.mint-swipe {
  height: 200px;
  text-align: center;
  img {
    height: 100%;
  }
  .full {
    width: 100%;
  }
}
</style>

