<template>
  <div class="list-content-big">
    <div class="list-content">
      <div class="goods-item" v-for="item in itemList" :key="item.id" @tap="goDetail(item.id)">
        <div class="goods-item-info">
          <img :src="item.img_url" alt>
          <p class="title">{{item.title}}</p>
        </div>
        <div class="goods-item-price">
          <p class="price">
            <span class="new">￥{{item.sell_price}}</span>
            <del class="old">￥{{item.market_price}}</del>
          </p>
          <p class="cell">
            <span>热卖中</span>
            <span>剩{{item.stock_quantity}}件</span>
          </p>
        </div>
      </div>
      
    </div>
    <mt-button size="large" type="danger" @click="getMore">加载更多</mt-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      itemIndex: 1,
      itemList: []
    };
  },
  created() {
    this.getGoodsItem();
  },
  methods: {
    getGoodsItem() {
      this.$http.get("api/getgoods?pageindex=" + this.itemIndex).then(res => {
        if (res.body.status == 0) {
          this.itemList = this.itemList.concat(res.body.message);
        }else {
            console.log(1111)
        }
      });
    },
    getMore() {
        this.itemIndex++;
        this.getGoodsItem();
    },
    goDetail(id){
        // this.$router.push("/home/goodsDetail/"+id);
        this.$router.push({name:"detail",params:{id}})
    }
  }
};
</script>
<style lang="less" scoped>
 .list-content-big{
    padding: 8px;
 }

.list-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .goods-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 49%;
    border: 1px solid #ccc;
    box-shadow: 0 0 5px #999;
    margin-bottom: 8px;
    padding: 3px;
    min-height: 300px;
    .goods-item-info {
      img {
        width: 100%;
      }
      .title {
        font-size: 15px;
        font-weight: 600;
        color: #333;
      }
    }
    .goods-item-price {
      background-color: #eee;
      p {
        margin: 0px;
      }
      .price {
        padding-top: 5px;
        .new {
          font-size: 17px;
          color: #f90707;
          font-weight: 700;
          margin: 0 10px 0 5px;
        }
        .old {
          font-size: 12px;
        }
      }

      .cell {
        display: flex;
        justify-content: space-between;
        padding: 5px;
        font-size: 13px;
      }
    }
  }
}
</style>
