<template>
  <div class="comment">
    <h3 class="comm-title">发表评论</h3>
    <hr>
    <textarea name="commConent" placeholder="请输入要bb的内容，不得少于120字" v-model="msg"></textarea>
    <mt-button type="primary" size="large" @click="postComment">发表评论</mt-button>
    <div class="comm-area" v-for="(item,i) in comments" :key="item.add_time">
      <p
        class="comm-header"
      >第{{ i+1 }}楼&nbsp;&nbsp;&nbsp;用户:{{ item.user_name }}&nbsp;&nbsp;发表时间:{{item.add_time|dataTime}}</p>
      <p class="comm-body">{{item.content==="undefined"?"此用户懒得要死，什么都没说":item.content}}</p>
    </div>
    <mt-button size="large" type="danger" plain @click="getMore">加载更多</mt-button>
  </div>
</template>
<script>
import { Toast } from "mint-ui"

export default {
  data() {
    return {
      pageindex: 1,
      comments: [],
      msg: ""
    };
  },
  created() {
    this.getComments()
  },
  methods: {
    getComments() {
      this.$http
        .get("api/getcomments/" + this.id + "?pageindex=" + this.pageindex)
        .then(res => {
          if (res.body.status === 0) {
            this.comments = this.comments.concat(res.body.message);
          } else {
            Toast("信息获取失败");
          }
        });
    },
    getMore() {
      this.pageindex++
      this.getComments()
    },
    postComment() {
      if (this.msg.trim().length === 0) {
        return Toast("小老弟先输入你要bb的内容");
      }
      this.$http
        .post("api/postcomment/" + this.$route.params.id, {
          content: this.msg.trim()
        })
        .then(res => {
          if (res.body.status === 0) {
            // 成功之后手动添加一组数据
            let newComm = {
              user_name: "匿名用户",
              add_time: Date.now(),
              content: this.msg.trim()
            }
            this.comments.unshift(newComm)
            // this.pageindex=1
            // this.getComments()
            this.msg=''
          } else {
            Toast("评论提交失败")
            this.msg=''
          }
        });
    }
  },
  props: ["id"]
};
</script>
<style lang="less" scoped>
.comment {
  padding-bottom: 10px;
  .comm-title {
    font-size: 20px;
  }
  textarea {
    height: 88px;
    margin: 0;
    font-size: 14px;
  }
  .comm-area {
    .comm-header {
      height: 30px;
      line-height: 30px;
      background-color: #aaa;
      margin-top: 6px;
      font-size: 12px;
      color: #111;
      padding-left: 5px;
    }
    .comm-body {
      font-size: 14px;
      padding-left: 20px;
      color: #222;
    }
  }
}
</style>
