import Vue from 'vue'
import VueRouter from 'vue-router';
// 引入对应的路由模板组件
import Home from './components/tabbar/home.vue'
import Number from './components/tabbar/number.vue'
import Shopcar from './components/tabbar/shopcar.vue'
import Search from './components/tabbar/search.vue'
import NewsList from './components/news/newsList.vue'
import NewsInfo from './components/news/newsInfo.vue'
import PhotoList from './components/photo/photoList.vue'
import PhotoInfo from './components/photo/photoInfo.vue'
import GoodsList from './components/shopping/goodsList.vue'
import GoodsDetail from './components/shopping/goodsDetail.vue'
import GoodsIntroduce from './components/shopping/goodsIntroduce.vue'
import CommMore from './components/shopping/commMore.vue'
// 安装路由
Vue.use(VueRouter)
export default new VueRouter({
    routes: [
        {path:'/',redirect:'./home'},
        { path: '/home', component: Home },
        { path: '/number', component: Number },
        { path: '/shopcar', component: Shopcar },
        { path: '/search', component: Search },
        { path: '/home/newsList', component: NewsList },
        { path: '/home/newsInfo/:id', component: NewsInfo },
        { path: '/home/photoList', component: PhotoList },
        { path: '/home/photoInfo/:id', component: PhotoInfo },
        { path: '/home/goodsList', component: GoodsList },
        { path: '/home/goodsDetail/:id', component: GoodsDetail,name:"detail"},
        { path: '/home/goodsIntroduce/:id', component: GoodsIntroduce},
        { path: '/home/commMore/:id', component: CommMore},
    ],
    linkActiveClass: 'mui-active'
})