import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
let shoppingCar = JSON.parse(localStorage.shoppingCar || '[]')
export default new Vuex.Store({
  state: {
    shoppingCar
  },
  mutations: {
    addShoppingCar(state, shoppingData) {
      // 先弄一个 标识接收
      let flag = false
      //如果购物车中本来就有 那就只要更新数量，购物车中没有 所以才要加入一个新的
      state.shoppingCar.some(item => {
        if (item.id == shoppingData.id) {
          // 更新数据 some()不能有 else
          item.count += parseInt(shoppingData.count)
          // console.log(item.count)
          flag = true
          return true
        }
      })
      if (!flag) {
        state.shoppingCar.push(shoppingData)
        // console.log(state.shoppingCar)
      }
      localStorage.shoppingCar = JSON.stringify(state.shoppingCar)
    },
    updateCar(state, carData) {
      state.shoppingCar.some(item => {
        if (item.id == carData.id) {
          item.count = parseInt(carData.count);
          return true;
        }
      })
      localStorage.shoppingCar = JSON.stringify(state.shoppingCar)
    },
    delCar(state, id) {
      state.shoppingCar.some((item, i) => {
        if (item.id === id) {
          shoppingCar.splice(i, 1)
        }
      })
      localStorage.shoppingCar = JSON.stringify(state.shoppingCar)
    },
    updateSelected(state, newSelected) {
      state.shoppingCar.some(item => {
        if (item.id === newSelected.id) {
          item.selected = newSelected.selected
          return true
        }
      })
      localStorage.shoppingCar = JSON.stringify(state.shoppingCar)

    }
  },
  getters: {
    updateCar(state) {
      let Count = 0
      state.shoppingCar.forEach(item => {
        Count += item.count
      });
      return Count
    },
    // 写出一个对象 键是id，值是count值
    getCount(state) {
      let carCount = {}
      state.shoppingCar.forEach(item => {
        carCount[item.id] = item.count
      })
      return carCount
    },
    getSelected(state) {
      let obj = {}
      state.shoppingCar.forEach(item => {
        obj[item.id] = item.selected
      })
      return obj
    },
    getCountandAmount(state) {
      let amountO = {
        count: 0,
        price: 0
      }
      state.shoppingCar.forEach(item=>{
        if(item.selected==true){
          amountO.count +=item.count
          amountO.price +=item.count*item.price
        }
      })
      return amountO
    }
  }
})
