<template>
    <div class="goods-introduce">
        <p class="goods-introduce-title">{{goodsIntroduce.title}}</p>
        <hr>
        <div v-html="goodsIntroduce.content"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            id:this.$route.params.id,
            goodsIntroduce:{}
        }
    },
    created() {
        this.getGoodsIntroduce()
    },
    methods: {
        getGoodsIntroduce(){
            this.$http.get('api/goods/getdesc/'+this.id).then(res=>{
                if(res.body.status==0){
                  this.goodsIntroduce=res.body.message[0]
                }
            })
        }
    },
}
</script>
<style lang="less">
.goods-introduce{
    .goods-introduce-title{
        color: #0056ff;
        margin: 15px 0;
        text-align: center;
        font-size: 15px;
        font-weight: 700;
    }
    img {
        width: 100%;
    }
}
</style>
