<template>
  <div class="car-content">
    <div class="mui-card" v-for="(item,i) in carData" :key="item.id">
      <div class="mui-card-content">
        <div class="mui-card-content-inner car-item">
          <mt-switch
            v-model="$store.getters.getSelected[item.id]"
            @change="changeSelected(item.id,$store.getters.getSelected[item.id])"
          ></mt-switch>
          <img :src="item.thumb_path" alt>
          <div class="car-info">
            <div class="car-info-titile">{{item.title}}</div>
            <p>
              <span class="new-price">￥{{item.sell_price}}</span>
              <carNumBox :goodscount="$store.getters.getCount[item.id]" :goodsid="item.id"></carNumBox>
              <a href="#" @click.prevent="del(item.id,i)">删除</a>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner amount">
          <p>
            总计（不含运费）
            <br>已勾选商品
            <span>{{$store.getters.getCountandAmount.count}}</span>，总价
            <span>￥{{$store.getters.getCountandAmount.price}}</span>
          </p>
          <mt-button size="small" type="danger">去结算</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import carNumBox from "../subcomponents/carNumberBox";
export default {
  data() {
    return {
      carData: []
    };
  },
  created() {
    this.getCarData();
  },
  methods: {
    getCarData() {
      let idArr = [];
      this.$store.state.shoppingCar.forEach(item => idArr.push(item.id));
      this.$http
        .get("api/goods/getshopcarlist/" + idArr.join(","))
        .then(res => {
          if (res.body.status == 0) {
            this.carData = res.body.message;
          }
        });
    },
    del(id, i) {
      this.carData.splice(i, 1);
      this.$store.commit("delCar", id);
    },
    changeSelected(id, val) {
      // console.log(id,val);
      this.$store.commit("updateSelected", { id: id, selected: val });
    }
  },
  components: {
    carNumBox
  }
};
</script>

<style lang="less" scoped>
.car-content {
  overflow: hidden;
  background-color: #ddd;
  .car-item {
    display: flex;
    align-items: center;
    img {
      height: 60px;
      width: 60px;
      margin: 0 5px;
    }
    .car-info {
      display: flex;
      flex-direction: column;

      .car-info-titile {
        font-size: 13px;
        color: #333;
        font-weight: 700;
        margin: 5px 0;
      }
      p {
        height: 35px;
        display: flex;
        line-height: 35px;

        .new-price {
          color: #f90707;
          font-size: 16px;
          font-weight: 600;
        }
        > div {
          margin: 0 5px;
        }
      }
    }
  }
  .amount {
    display: flex;
    align-items: center;
    justify-content: space-around;
    p {
      line-height: 25px;
      span {
        font-size: 16px;
        font-weight: 600;
        color: #f90707;
      }
    }
  }
}
.mui-card-content-inner {
  padding: 15px 10px;
}
</style>